@charset "UTF-8";
body {
  background-color: #e4e4e4;
  font-family: Roboto;
  font-size: 16px;
  
  
  
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  color: inherit;
  text-decoration: none;
}

.product-card {
  position: relative;
  margin-bottom: 26px;
  transition: transform 0.5s;
  animation: bounceInUp;
  animation-duration: 1s
}
.product-card:hover, .product-card:focus, .product-card:active {
  transform: translateY(-8px);
}
.product-card__content {
  height: 100%;
  border-radius: 6px;
  padding: 24px 16px 36px;
  background-color: #fff;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  flex-direction: column;
}
.product-card__content:hover, .product-card__content:focus, .product-card__content:active {
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.2);
  color: inherit;
  text-decoration: none;
}
.product-card__actions {
  position: absolute;
  bottom: -20px;
  margin: 0 24px;
  left: 24px;
  right: 24px;
  display: flex;
  justify-content: center;
}
.product-card__icon-btn {
  height: 40px;
  width: 40px;
  min-height: 40px;
  min-width: 40px;
  background-color: #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  color: #fb6969;
  box-shadow: 0 2px 5px 2px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out, color 0.3s ease-in-out, background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.product-card__icon-btn:hover, .product-card__icon-btn:active, .product-card__icon-btn:focus {
  text-decoration: none;
  color: #fff;
  background-color: #fa5050;
  box-shadow: 0 2px 5px 2px rgba(251, 105, 105, 0.6);
  transform: translateY(-3px);
}
.product-card__btn {
  border-radius: 32px;
  text-align: center;
  white-space: nowrap;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 600;
  background-color: #fb6969;
  box-shadow: 0 2px 5px 2px rgba(251, 105, 105, 0.4);
  transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out, color 0.3s ease-in-out;
  box-sizing: border-box;
  padding: 0 2.5em;
  height: 40px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 10px;
}
.product-card__btn:hover, .product-card__btn:focus, .product-card__btn:active {
  text-decoration: none;
  color: #fb6969;
  background-color: #fff;
  box-shadow: 0 2px 10px 2px rgba(0, 0, 0, 0.1);
  transform: translateY(-3px);
}
.product-card__btn--add-to-cart:before {
  content: "ÔÅß";
  font-family: "Font Awesome 5 Pro";
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.5rem;
}
.product-card__img {
  display: block;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 24px;
  height: 300px;
}
.product-card__title {
  font-weight: 500;
  font-size: 16px;
  text-align: center;
  margin-bottom: 12px;
}
.product-card__subtitle {
  font-size: 13px;
  color: #b0b0b0;
  text-align: center;
}
.product-card__price {
  font-size: 24px;
  text-align: center;
  margin-bottom: 12px;
  font-weight: bolder;
}
.product-card__reviews {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 13px;
}